import * as React from "react"
import AppBar from "@mui/material/AppBar"
import Box from "@mui/material/Box"
import Toolbar from "@mui/material/Toolbar"
import IconButton from "@mui/material/IconButton"
import Typography from "@mui/material/Typography"
import Menu from "@mui/material/Menu"
import MenuIcon from "@mui/icons-material/Menu"
import Container from "@mui/material/Container"
import Avatar from "@mui/material/Avatar"
import Tooltip from "@mui/material/Tooltip"
import MenuItem from "@mui/material/MenuItem"
import { makeStyles } from "@mui/styles"
import { Badge, Button } from "@mui/material"
import { Link } from "react-router-dom"
import UserContext from "../context/UserContext"
import LocalMallIcon from "@mui/icons-material/LocalMall"
import ScrollHandler from "../components/ScrollHandler"

const pages = [
    { name: "Home", hasLink: "" },
    { name: "Services", hasLink: "ourServices" },
    { name: "Product", hasLink: "ourProducts" },
    { name: "About Us", hasLink: "aboutUs" },
    { name: "Contact Us", hasLink: "contactUs" },
]

const useStyles = makeStyles((theme) => ({
    appbar: {
        backgroundColor: "#c2e5dc4a !important",
    },
    navigation: {
        margin: "0 15px !important",
        textTransform: "uppercase",
        padding: "10px 5px",
        borderBottom: "3px solid #c2e5dc4a",
        cursor: "pointer",
        transition: "0.4s all ease-in",
        "&:hover": {
            color: `${theme.palette.primary.main}`,
            borderBottom: `3px solid ${theme.palette.primary.main}`,
            opacity: 0.8,
        },
    },
}))

const Header = () => {
    const [anchorElNav, setAnchorElNav] = React.useState(null)
    const [anchorElUser, setAnchorElUser] = React.useState(null)

    const userContext = React.useContext(UserContext)
    const userState = userContext.state
    const dispatch = userContext.useDispatch

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget)
    }
    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget)
    }

    const handleCloseNavMenu = () => {
        setAnchorElNav(null)
    }

    const handleCloseUserMenu = () => {
        setAnchorElUser(null)
    }

    const classes = useStyles()

    return (
        <AppBar position="static" className={classes.appbar} elevation={0}>
            <Container maxWidth="xl">
                <Toolbar disableGutters>
                    <Box
                        sx={{ mr: 2, display: { xs: "none", md: "flex" } }}
                        flexGrow={1}
                        noWrap
                        component="div"
                    >
                        {/* <img src={mfoundLogo} alt="M Found Logo" /> */}
                        <Link to="/">
                            <Typography variant="h4" color="primary">
                                MFOUND
                            </Typography>
                        </Link>
                    </Box>
                    {/* xs flex */}
                    <Box
                        sx={{
                            flexGrow: 1,
                            display: { xs: "none", md: "none" },
                        }}
                    >
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleOpenNavMenu}
                            color="inherit"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorElNav}
                            anchorOrigin={{
                                vertical: "bottom",
                                horizontal: "left",
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: "top",
                                horizontal: "left",
                            }}
                            open={Boolean(anchorElNav)}
                            onClose={handleCloseNavMenu}
                            sx={{
                                display: { xs: "block", md: "none" },
                            }}
                        >
                            {pages.map((page) => (
                                <MenuItem
                                    key={page}
                                    onClick={handleCloseNavMenu}
                                >
                                    <Link
                                        to={`${process.env.PUBLIC_URL}/#${page.hasLink}`}
                                    >
                                        <Typography
                                            textAlign="center"
                                            variant="body1"
                                        >
                                            {page.name}
                                        </Typography>
                                    </Link>
                                </MenuItem>
                            ))}
                        </Menu>
                    </Box>
                    <Box
                        noWrap
                        component="div"
                        sx={{
                            flexGrow: 1,
                            display: { xs: "flex", md: "none" },
                        }}
                    >
                        <Typography
                            variant="h4"
                            color="primary"
                            component={Link}
                            to="/"
                        >
                            MFOUND
                        </Typography>
                    </Box>
                    <Box
                        sx={{
                            flexGrow: 1,
                            display: { xs: "none", md: "flex" },
                        }}
                    >
                        {pages.map((page) => (
                            <Link
                                to={`${process.env.PUBLIC_URL}/#${page.hasLink}`}
                                key={page}
                            >
                                <Typography
                                    variant="body2"
                                    onClick={handleCloseNavMenu}
                                    sx={{
                                        my: 2,
                                        color: "primary",
                                        display: "block",
                                    }}
                                    className={classes.navigation}
                                >
                                    {page.name}
                                </Typography>
                            </Link>
                        ))}
                    </Box>

                    <Box sx={{ flexGrow: 0 }}>
                        {typeof userState.loggedIn !== "undefined" &&
                        userState.loggedIn ? (
                            <React.Fragment>
                                <IconButton
                                    aria-label="cart"
                                    sx={{
                                        marginRight: 1,
                                    }}
                                    component={Link}
                                    to={`${process.env.PUBLIC_URL}/cart`}
                                >
                                    <Badge
                                        badgeContent={
                                            userState.cart !== null &&
                                            userState.cart.length
                                        }
                                        color="secondary"
                                    >
                                        <LocalMallIcon
                                            sx={{
                                                fontSize: 32,
                                                color: "#2EA786",
                                            }}
                                        />
                                    </Badge>
                                </IconButton>

                                <Tooltip title="Open settings">
                                    <IconButton
                                        onClick={handleOpenUserMenu}
                                        sx={{ p: 0 }}
                                    >
                                        <Avatar
                                            alt={userState.profile?.first_name}
                                            src="/static/images/avatar/2.jpg"
                                        />
                                    </IconButton>
                                </Tooltip>
                                <Menu
                                    sx={{ mt: "45px" }}
                                    id="menu-appbar"
                                    anchorEl={anchorElUser}
                                    anchorOrigin={{
                                        vertical: "top",
                                        horizontal: "right",
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: "top",
                                        horizontal: "right",
                                    }}
                                    open={Boolean(anchorElUser)}
                                    onClose={handleCloseUserMenu}
                                >
                                    <MenuItem onClick={handleCloseNavMenu}>
                                        <Typography
                                            textAlign="center"
                                            component={Link}
                                            to="/profile"
                                        >
                                            Profile
                                        </Typography>
                                    </MenuItem>
                                    <MenuItem
                                        onClick={() => {
                                            dispatch({ type: "LOGOUT" })
                                            handleCloseNavMenu()
                                        }}
                                    >
                                        <Typography textAlign="center">
                                            Logout
                                        </Typography>
                                    </MenuItem>
                                </Menu>
                            </React.Fragment>
                        ) : (
                            <Button variant="outlined">
                                <Link to={`${process.env.PUBLIC_URL}/login`}>
                                    Login
                                </Link>
                            </Button>
                        )}
                    </Box>
                </Toolbar>
            </Container>
            <ScrollHandler />
        </AppBar>
    )
}
export default Header
