import { FormControl, InputLabel, MenuItem, Select } from "@mui/material"
import React, { useState } from "react"
import { Link } from "react-router-dom"
import MFoundLogo from "../images/logo-mfound.png"
import { useAuthFormClasses } from "../theming"
import ItemFound from "../components/found-components/ItemFound"
import PersonFound from "../components/found-components/PersonFound"

const FoundItem = () => {
    const classes = useAuthFormClasses()
    const [lostType, setLostType] = useState("item")

    const handleLostType = (e) => {
        let value = e.target.value
        setLostType(value)
    }

    return (
        <div className={classes.root}>
            <div className={classes.formWrapper}>
                <div className={classes.logoWrapper}>
                    <Link to={`${process.env.PUBLIC_URL}`}>
                        <img src={MFoundLogo} width={250} alt="MFound" />
                    </Link>
                </div>
                <div className={classes.inputWrapper}>
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">
                            Select Type Of Lost
                        </InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={lostType}
                            label="type-of-lost"
                            onChange={handleLostType}
                        >
                            <MenuItem value={"item"}>Found An Item</MenuItem>
                            <MenuItem value={"person"}>Found A Person</MenuItem>
                        </Select>
                    </FormControl>
                </div>
                {lostType === "item" && <ItemFound />}
                {lostType === "person" && <PersonFound />}
            </div>
        </div>
    )
}

export default FoundItem
