import { Backdrop, CircularProgress } from "@mui/material"
import axios from "axios"
import { useEffect, useState, useReducer } from "react"
import { fetchUser, getCart } from "./action/FetchData"
import AppRoutes from "./AppRoutes"
import ProductsContext from "./context/ProductsContext"
import UserContext from "./context/UserContext"
import { ApiURL } from "./utils/Server"

const initialState = {
    profile: null,
    cart: null,
    loggedIn: false,
    request: false,
}

function reducer(state, action) {
    switch (action.type) {
        case "GET_CART":
            if (action.payload.profile !== "undefined")
                return {
                    ...state,
                    profile: action.payload.profile,
                    cart: action.payload.cart,
                    loggedIn: true,
                }
            return {
                ...state,
                cart: action.payload.cart,
                loggedIn: true,
            }
        case "LOGIN":
            localStorage.setItem("m-token", action.payload.token)
            localStorage.setItem("m-id", action.payload.id)
            return {
                ...state,
                loggedIn: true,
            }
        case "LOGOUT":
            localStorage.removeItem("m-token")
            localStorage.removeItem("m-id")
            return {
                ...state,
                profile: null,
                cart: null,
                loggedIn: false,
            }
        case "REQUEST":
            return {
                ...state,
                request: action.payload,
            }
        default:
            return state
    }
}

function App() {
    const [products, setProducts] = useState(null)
    const [user, dispatch] = useReducer(reducer, initialState)

    useEffect(() => {
        axios
            .get(`${ApiURL}products/admin/all`)
            .then((res) => {
                if (res.data && typeof res.data.result !== "undefined") {
                    setProducts(res.data.result)
                } else {
                    console.log("Api Error, didn't find the result")
                }
            })
            .catch((error) => {
                console.log(error)
            })
    }, [])

    useEffect(() => {
        const fetchCartItem = async () => {
            if (localStorage.getItem("m-id")) {
                let profile = await fetchUser()
                let result = await getCart()
                if (typeof result.data !== "undefined") {
                    dispatch({
                        type: "GET_CART",
                        payload: {
                            cart: result.data.result,
                            profile: profile.data.result,
                        },
                    })
                }
            }
        }
        fetchCartItem()
    }, [user.loggedIn])

    return (
        <ProductsContext.Provider value={products}>
            <UserContext.Provider
                value={{ state: user, useDispatch: dispatch }}
            >
                <AppRoutes />
                <Backdrop
                    sx={{
                        color: "#fff",
                        zIndex: (theme) => theme.zIndex.drawer + 1,
                    }}
                    open={user.request}
                >
                    <CircularProgress color="inherit" />
                </Backdrop>
            </UserContext.Provider>
        </ProductsContext.Provider>
    )
}

export default App
