const slugifyString = (string) => {
    return string
        .toLowerCase()
        .replace(/ /g, "-")
        .replace(/[^\w-]+/g, "")
}

const rupeeFy = (number) => {
    if (number) {
        let string = number.toString().split(",").join("")
        return string.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") + ".00"
    }
    return 0
}

const dateToday = () => {
    var today = new Date()
    var dd = String(today.getDate()).padStart(2, "0")
    var mm = String(today.getMonth() + 1).padStart(2, "0")
    var yyyy = today.getFullYear()

    today = yyyy + "-" + mm + "-" + dd
    return today
}

export { slugifyString, rupeeFy, dateToday }
