import {
    FormControl,
    InputLabel,
    MenuItem,
    Select,
    Input,
    TextField,
    FormControlLabel,
    Switch,
    Button,
} from "@mui/material"
import axios from "axios"
import { useEffect, useState } from "react"
import { dateToday } from "../../helpers"
import { useAuthFormClasses } from "../../theming"
import { ApiURL } from "../../utils/Server"

const ItemFound = () => {
    const [categories, setCategories] = useState()
    const [wouldRewarded, setWouldRewarded] = useState()
    const classes = useAuthFormClasses()

    useEffect(() => {
        axios
            .get(ApiURL + "category/lost")
            .then((result) => {
                setCategories(result.data.result)
            })
            .catch((error) => {
                console.log(error)
            })
    }, [])

    return (
        <div>
            <div className={classes.inputWrapper}>
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">
                        Choose Category
                    </InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        label="choose category"
                    >
                        {categories &&
                            categories.length > 0 &&
                            categories.map((category, index) => {
                                return (
                                    <MenuItem
                                        value={category.name}
                                        key={category.name}
                                    >
                                        {category.name}
                                    </MenuItem>
                                )
                            })}
                    </Select>
                </FormControl>
                <div className={classes.inputWrapper}>
                    <Input
                        required
                        id="image"
                        type="file"
                        label="Image"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <TextField
                        required
                        id="typeOfFound"
                        label="Type of Found"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <TextField
                        required
                        id="contactNumber"
                        label="Contact Number"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <FormControlLabel
                        value="1"
                        control={
                            <Switch
                                color="primary"
                                onClick={(e) => {
                                    setWouldRewarded(e.target.checked)
                                }}
                            />
                        }
                        label="Would you like to give a reward?"
                        labelPlacement="start"
                    />
                </div>
                {wouldRewarded && (
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="contactNumber"
                            label="Reward Rs."
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>
                )}
                <div className={classes.inputWrapper}>
                    <TextField
                        required
                        type="date"
                        defaultValue={dateToday()}
                        id="contactNumber"
                        label="Date of found"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <TextField
                        required
                        id="contactNumber"
                        label="Where have you found?"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <InputLabel sx={{ fontSize: "1.2rem" }} color="success">
                        Brand Information
                    </InputLabel>
                </div>
                <div className={classes.inputWrapper}>
                    <TextField
                        required
                        id="contactNumber"
                        label="Brand"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <TextField
                        required
                        id="contactNumber"
                        label="Color"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <TextField
                        id="contactNumber"
                        label="Description"
                        autoComplete="off"
                        sx={{ width: "100%" }}
                    />
                </div>
            </div>
            <div className={classes.formActionWrapper}>
                <Button variant="contained" type="submit">
                    Proceed
                </Button>
            </div>
        </div>
    )
}

export default ItemFound
