import { Button, Container, Grid, Typography } from "@mui/material"
import axios from "axios"
import React, { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import Footer from "../components/Footer"
import UserContext from "../context/UserContext"
import Header from "../layouts/Header"
import { ApiURL } from "../utils/Server"

export default function ScannedProduct() {
    const [data, setData] = useState(null)
    let params = useParams()
    const navigate = useNavigate()

    const [latLong, setLatLong] = useState()

    useEffect(() => {
        navigator.geolocation.getCurrentPosition(function (position) {
            setLatLong({
                lat: position.coords.latitude,
                long: position.coords.longitude,
            })
        })
    }, [])

    const userContext = React.useContext(UserContext)
    const userState = userContext.state

    useEffect(() => {
        const fetchData = async () => {
            try {
                const parsedData = await axios.post(
                    `${ApiURL}get_qr_code_status`,
                    {
                        qr_code: `https://mfound.in/lost&found/${params.jsonData}`,
                    }
                )
                setData({
                    ...parsedData.data.result,
                    image: parsedData.data.result.image.split(","),
                })
            } catch (e) {
                console.log(e)
                alert("invalid url provided")
            }
        }
        fetchData()
    }, [params])

    const submitData = async () => {
        const formData = new FormData()
        formData.append("type", "itemQR")
        formData.append("name", data?.name)
        formData.append("mobile", userState.profile?.mobile)
        formData.append(
            "dateOfFound",
            new Date().toISOString().slice(0, 10).replace("T", " ")
        )
        formData.append("latLocation", latLong.lat)
        formData.append("longLocation", latLong.long)
        formData.append("addressLocation", userState.profile?.address)
        formData.append("category", data?.category)
        formData.append("brand", data?.brand)
        formData.append("color", data?.color)
        formData.append("description", data.description)
        formData.append(
            "qr_code",
            `https://mfound.in/lost&found/${params.jsonData}`
        )

        const config = {
            headers: {
                "content-type": "multipart/form-data",
            },
        }

        try {
            await axios.post(`${ApiURL}found`, formData, config)
            alert(
                "we have successfully send your found item information, our team will get back to you soon"
            )
            navigate("/")
        } catch (e) {
            console.log(e)
            alert("Oops, soemthing went wrong try later")
        }
    }

    const handleClick = () => {
        if (!userState.loggedIn) {
            localStorage.setItem("barcode", params.jsonData)
            navigate(`${process.env.PUBLIC_URL}/login`)
            return false
        } else {
            localStorage.removeItem("barcode")
            submitData()
        }
    }

    return (
        <>
            <div className="head">
                <Header />
                <div className="root">
                    <div
                        className="bg-white section-block"
                        style={{ paddingTop: 10 }}
                    >
                        {data && (
                            <Container maxWidth="xl">
                                <Grid container>
                                    <Grid item xs={12} md={6}>
                                        <div style={{ maxWidth: "100%" }}>
                                            <img
                                                src={`https://s3.us-east-2.amazonaws.com/www.mfound.in/qr_codes/${data?.image[0]}`}
                                                alt={data?.name}
                                                height={500}
                                            />
                                        </div>
                                    </Grid>
                                    <Grid item xs={12} md={6} pl={1}>
                                        <Typography variant="h4">
                                            {data?.name}
                                        </Typography>
                                        {Object.keys(data).map((key) => {
                                            if (
                                                key === "id" ||
                                                key === "name" ||
                                                key === "image" ||
                                                key === "user_id" ||
                                                key === "qr_code" ||
                                                key === "status" ||
                                                key === "created_at" ||
                                                key === "description"
                                            ) {
                                                return ""
                                            } else {
                                                return (
                                                    <div key={key}>
                                                        <Typography
                                                            variant="body1"
                                                            color="secondary"
                                                            mt={2}
                                                        >
                                                            {key}
                                                        </Typography>
                                                        <Typography
                                                            variant="body1"
                                                            color="primary"
                                                            fontWeight={600}
                                                        >
                                                            {data[key]}
                                                        </Typography>
                                                    </div>
                                                )
                                            }
                                        })}

                                        <Typography
                                            variant="body1"
                                            color="secondary"
                                            mt={2}
                                        >
                                            Description
                                        </Typography>
                                        <Typography variant="body1">
                                            {data?.description}
                                        </Typography>

                                        <Button
                                            variant="contained"
                                            sx={{ mt: 4 }}
                                            onClick={handleClick}
                                        >
                                            Submit
                                        </Button>
                                    </Grid>
                                </Grid>
                            </Container>
                        )}
                    </div>
                </div>
                <Footer />
            </div>
        </>
    )
}
