import { Box, Button, Container, Grid, Typography } from "@mui/material"
import React, { useContext, useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import Footer from "../components/Footer"
import Header from "../layouts/Header"
import CurrencyRupeeIcon from "@mui/icons-material/CurrencyRupee"
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart"
import BoltIcon from "@mui/icons-material/Bolt"
import axios from "axios"
import { ApiURL } from "../utils/Server"
import UserContext from "../context/UserContext"
import { getCart } from "../action/FetchData"
import FreeShipping from "../components/FreeShipping"

const Product = () => {
    const [product, setProduct] = useState(null)
    const [products, setProducts] = useState(null)
    let params = useParams()
    const navigate = useNavigate()

    const user = useContext(UserContext)
    const userState = user.state
    const dispatch = user.useDispatch

    const getProducts = async () => {
        dispatch({ type: "REQUEST", payload: true })
        try {
            let result = await axios.post(`${ApiURL}category_based_products`, {
                product_id: params.categoryId,
            })
            setProducts(result.data.result)
            dispatch({ type: "REQUEST", payload: false })
        } catch (err) {
            dispatch({ type: "REQUEST", payload: false })
            console.log(err)
        }
    }

    useEffect(() => {
        getProducts()

        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        if (products && products.length > 0) {
            let findProduct = products.find(
                (item) => item.id === parseInt(params.productId)
            )
            setProduct(findProduct)
        }
    }, [products, params])

    const addToCart = () => {
        if (
            !(typeof userState.loggedIn !== "undefined" && userState.loggedIn)
        ) {
            navigate(`${process.env.PUBLIC_URL}/login`)
        } else {
            dispatch({ type: "REQUEST", payload: true })
            axios
                .post(`${ApiURL}save_cart`, {
                    product_id: parseInt(product.id),
                    user_id: parseInt(localStorage.getItem("m-id")),
                    quantity: 1,
                })
                .then(() => {
                    fetchCartItem()
                })
                .catch((err) => {
                    console.log("error", err)
                })
                .then(() => {
                    dispatch({ type: "REQUEST", payload: false })
                })
        }
    }

    const buyNow = () => {
        if (
            !(typeof userState.loggedIn !== "undefined" && userState.loggedIn)
        ) {
            navigate(`${process.env.PUBLIC_URL}/login`)
        } else {
            addToCart()
            navigate(`${process.env.PUBLIC_URL}/cart`)
        }
    }

    const fetchCartItem = async () => {
        let result = await getCart()
        dispatch({ type: "GET_CART", payload: { cart: result.data.result } })
    }

    return (
        <React.Fragment>
            <div className="head">
                <Header />
                <div className="root">
                    <div
                        className="bg-white section-block"
                        style={{ paddingTop: 10 }}
                    >
                        <Container maxWidth="xl">
                            <FreeShipping />
                            <Grid container>
                                <Grid item xs={12} md={6}>
                                    {product && (
                                        <div style={{ maxWidth: "100%" }}>
                                            <img
                                                src={`https://s3.us-east-2.amazonaws.com/www.mfound.in/products_category/${product.image}`}
                                                alt={product.name}
                                                height={500}
                                            />
                                        </div>
                                    )}
                                </Grid>
                                <Grid item xs={12} md={6} pl={1}>
                                    {product && (
                                        <React.Fragment>
                                            <Typography variant="h4">
                                                {product.name}
                                            </Typography>
                                            <Typography
                                                variant="body1"
                                                color="secondary"
                                                mt={2}
                                            >
                                                Special Price
                                            </Typography>
                                            <Typography
                                                variant="h4"
                                                color="primary"
                                            >
                                                <CurrencyRupeeIcon
                                                    sx={{
                                                        fontWeight: 600,
                                                    }}
                                                />
                                                {product.price} /-
                                            </Typography>
                                            <Typography
                                                variant="body1"
                                                color="secondary"
                                                mt={2}
                                            >
                                                Description
                                            </Typography>
                                            <Typography
                                                variant="body1"
                                                className="border-left"
                                                my={1}
                                            >
                                                {product.description}
                                            </Typography>
                                            <Typography
                                                variant="body1"
                                                color="secondary"
                                                mt={2}
                                            >
                                                Size
                                            </Typography>
                                            <Typography
                                                variant="body1"
                                                color="primary"
                                                fontWeight={600}
                                            >
                                                {product.size}
                                            </Typography>
                                            <Box
                                                display="flex"
                                                sx={{ maxWidth: 350, mt: 2 }}
                                                justifyContent="space-between"
                                            >
                                                <Button
                                                    variant="outlined"
                                                    color="primary"
                                                    size="large"
                                                    startIcon={
                                                        <ShoppingCartIcon />
                                                    }
                                                    onClick={() => {
                                                        addToCart()
                                                    }}
                                                >
                                                    Add To Cart
                                                </Button>
                                                <Button
                                                    variant="contained"
                                                    color="primary"
                                                    size="large"
                                                    startIcon={<BoltIcon />}
                                                    onClick={buyNow}
                                                >
                                                    Buy Now
                                                </Button>
                                            </Box>
                                        </React.Fragment>
                                    )}
                                </Grid>
                            </Grid>
                        </Container>
                    </div>
                </div>
                <Footer />
            </div>
        </React.Fragment>
    )
}

export default Product
