import {
    Button,
    FormControl,
    InputLabel,
    MenuItem,
    Select,
    TextField,
    Typography,
} from "@mui/material"
import React, { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import MFoundLogo from "../images/logo-mfound.png"
import { Box } from "@mui/system"
import { useAuthFormClasses } from "../theming"
import axios from "axios"
import { ApiURL } from "../utils/Server"
import Items from "../components/lost-components/Items"
import Travel from "../components/lost-components/Travel"
import Person from "../components/lost-components/Person"

const LostItem = () => {
    const classes = useAuthFormClasses()
    const [lostType, setLostType] = useState("item")
    const [categories, setCategories] = useState()
    const [latLong, setLatLong] = useState()

    const handleLostType = (e) => {
        let value = e.target.value
        setLostType(value)
    }

    useEffect(() => {
        navigator.geolocation.getCurrentPosition(function (position) {
            setLatLong({
                lat: position.coords.latitude,
                long: position.coords.longitude,
            })
        })
    }, [])

    return (
        <div className={classes.root}>
            <div className={classes.formWrapper}>
                <div className={classes.logoWrapper}>
                    <Link to={`${process.env.PUBLIC_URL}`}>
                        <img src={MFoundLogo} width={250} alt="MFound" />
                    </Link>
                </div>
                <div className={classes.inputWrapper}>
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">
                            Select Type Of Lost
                        </InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={lostType}
                            label="type-of-lost"
                            onChange={handleLostType}
                        >
                            <MenuItem value={"item"}>Lost An Item</MenuItem>
                            <MenuItem value={"travel"}>Lost In Travel</MenuItem>
                            <MenuItem value={"person"}>Person Lost</MenuItem>
                        </Select>
                    </FormControl>
                </div>
                {lostType === "item" && <Items />}
                {lostType === "travel" && <Travel />}
                {lostType === "person" && <Person />}
            </div>
        </div>
    )
}

export default LostItem
