import { Button, TextField, Typography } from "@mui/material"
import React, { useContext, useEffect, useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import MFoundLogo from "../images/logo-mfound.png"
import { Box } from "@mui/system"
import { useAuthFormClasses } from "../theming"
import UserContext from "../context/UserContext"
import { useFormik } from "formik"
import axios from "axios"
import { ApiURL } from "../utils/Server"

const OtpLogin = () => {
    const classes = useAuthFormClasses()
    const [showOtp, setShowOtp] = useState(false)
    const userContext = useContext(UserContext)
    const userState = userContext.state
    const dispatch = userContext.useDispatch

    const navigate = useNavigate()

    useEffect(() => {
        if (userState.loggedIn && localStorage.getItem("barcode")) {
            navigate(
                `${
                    process.env.PUBLIC_URL
                }/scan-lost-item/${localStorage.getItem("barcode")}`
            )
        } else if (userState.loggedIn) {
            navigate("/")
        }
    }, [navigate, userState])

    const getOtp = (values) => {
        dispatch({ type: "REQUEST", payload: true })
        axios
            .post(`${ApiURL}auth/login-via-otp`, {
                mobile: values.mobile,
                device: "web",
                role: "users",
            })
            .then((res) => {
                setShowOtp(res.data.result.id)
                alert("otp has been sent to your mobile")
            })
            .catch((err) => {
                alert("oops, it seems we do not have your number registered")
            })
            .then(() => {
                dispatch({ type: "REQUEST", payload: false })
            })
    }

    const getLoggedIn = (values) => {
        dispatch({ type: "REQUEST", payload: true })
        axios
            .post(`${ApiURL}auth/login-verify-otp`, {
                mobile: values.mobile,
                otp: values.otp,
                id: showOtp,
                deviceToken: "web",
                device: "web",
                role: "users",
            })
            .then((res) => {
                if (typeof res.data.result !== "undefined")
                    dispatch({ type: "LOGIN", payload: res.data.result })
            })
            .catch((err) => {
                console.log(err)
                dispatch({
                    type: "LOGOUT",
                })
            })
            .then(() => {
                dispatch({ type: "REQUEST", payload: false })
            })
    }

    const formik = useFormik({
        initialValues: {
            mobile: "",
            otp: "",
        },
        onSubmit: (values) => {
            if (showOtp) getLoggedIn(values)
            else getOtp(values)
        },
    })

    return (
        <div className={classes.root}>
            <div className={classes.formWrapper}>
                <form onSubmit={formik.handleSubmit}>
                    <div className={classes.logoWrapper}>
                        <Link to={`${process.env.PUBLIC_URL}`}>
                            <img src={MFoundLogo} width={250} alt="MFound" />
                        </Link>
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="mobile"
                            onChange={formik.handleChange}
                            value={formik.values.mobile}
                            label="mobile"
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    {showOtp && (
                        <div className={classes.inputWrapper}>
                            <TextField
                                required
                                type="otp"
                                id="otp"
                                onChange={formik.handleChange}
                                value={formik.values.otp}
                                label="otp"
                                autoComplete="off"
                                sx={{ width: "100%" }}
                            />
                        </div>
                    )}
                    <div className={classes.socialIconWrapper}>
                        <Link to={`${process.env.PUBLIC_URL}/login`}>
                            Login with password
                        </Link>
                    </div>
                    <div className={classes.formActionWrapper}>
                        <Button variant="contained" type="submit">
                            {showOtp ? "Sign In" : "Get Otp"}
                        </Button>
                        <Box sx={{ display: "flex", padding: "20px 0" }}>
                            <Typography variant="body1" sx={{ mr: 1 }}>
                                Don't have an account?
                            </Typography>
                            <Link to={`${process.env.PUBLIC_URL}/register`}>
                                click here
                            </Link>
                        </Box>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default OtpLogin
