import { Box, Container, Grid, Typography } from "@mui/material"
import React, { useContext, useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import Footer from "../components/Footer"
import ProductCard from "../components/ProductCard"
import ProductsContext from "../context/ProductsContext"
import { slugifyString } from "../helpers"
import Header from "../layouts/Header"
import axios from "axios"
import { ApiURL } from "../utils/Server"
import FreeShipping from "../components/FreeShipping"

const Category = () => {
    const [cProducts, setCProducts] = useState(null)
    const [product, setProduct] = useState(null)
    let params = useParams()
    const products = useContext(ProductsContext)

    useEffect(() => {
        if (products && products.length > 0) {
            setProduct(
                products.find(
                    (item) => slugifyString(item.name) === params.categoryName
                )
            )
            if (product) {
                axios
                    .post(`${ApiURL}category_based_products`, {
                        product_id: product.id,
                    })
                    .then((res) => {
                        setCProducts(res.data.result)
                    })
                    .catch((err) => {
                        console.log(err)
                    })
            }
        }
    }, [products, params, product])

    return (
        <React.Fragment>
            <div className="head">
                <Header />
                <div className="root">
                    <div
                        className="bg-white section-block"
                        style={{ paddingTop: 10 }}
                    >
                        <Container maxWidth="xl">
                            <FreeShipping />
                            <Grid container>
                                <Grid item xs={12} md={6}>
                                    {product && (
                                        <div style={{ maxWidth: "100%" }}>
                                            <img
                                                src={`https://s3.us-east-2.amazonaws.com/www.mfound.in/products/${product.image}`}
                                                alt={product.name}
                                                height={500}
                                            />
                                        </div>
                                    )}
                                </Grid>
                                <Grid item xs={12} md={6} pl={1}>
                                    {product && (
                                        <React.Fragment>
                                            <Typography variant="h4">
                                                {product.name}
                                            </Typography>
                                            <Typography
                                                variant="body1"
                                                color="secondary"
                                                mt={2}
                                            >
                                                Description
                                            </Typography>
                                            <Typography
                                                variant="body1"
                                                className="border-left"
                                                my={1}
                                            >
                                                {product.description}
                                            </Typography>
                                        </React.Fragment>
                                    )}
                                </Grid>
                            </Grid>
                        </Container>
                    </div>
                    <div className="bg-light section-block">
                        <Container maxWidth="xl">
                            <Grid container>
                                <Grid item xs={12} md={12}>
                                    <Box
                                        sx={{ py: 4 }}
                                        display="flex"
                                        justifyContent={`space-around`}
                                        flexWrap="wrap"
                                    >
                                        {product !== null &&
                                            cProducts !== null &&
                                            cProducts.length > 0 &&
                                            cProducts
                                                .filter(
                                                    (x) =>
                                                        x.id !== product.id &&
                                                        x.price > 0
                                                )
                                                .map((item, index) => {
                                                    return (
                                                        <ProductCard
                                                            product={item}
                                                            id={product.id}
                                                            key={index}
                                                        />
                                                    )
                                                })}
                                    </Box>
                                </Grid>
                            </Grid>
                        </Container>
                    </div>
                </div>
                <Footer />
            </div>
        </React.Fragment>
    )
}

export default Category
