import Home from "./Home"
import { Routes, Route } from "react-router-dom"
import Login from "./views/Login"
import Register from "./views/Register"
import ForgetPassword from "./views/ForgetPassword"
import Product from "./views/Product"
import Cart from "./views/Cart"
import Category from "./views/Category"
import LostItem from "./views/LostItem"
import FoundItem from "./views/FoundItem"
import OtpLogin from "./views/OtpLogin"
import OrderSuccess from "./views/OrderSuccess"
import Profile from "./views/Profile"
import ScannedProduct from "./views/ScannedProduct"

const AppRoutes = () => {
    return (
        <Routes>
            <Route path={`${process.env.PUBLIC_URL}`} element={<Home />} />
            <Route
                path={`${process.env.PUBLIC_URL}/login`}
                element={<Login />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/otp-login`}
                element={<OtpLogin />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/forget-password`}
                element={<ForgetPassword />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/register`}
                element={<Register />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/product/:categoryId/:productId`}
                element={<Product />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/:categoryName`}
                element={<Category />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/lost-an-item`}
                element={<LostItem />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/found-an-item`}
                element={<FoundItem />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/order-placed`}
                element={<OrderSuccess />}
            />
            <Route
                path={`${process.env.PUBLIC_URL}/profile`}
                element={<Profile />}
            />
            <Route path={`${process.env.PUBLIC_URL}/cart`} element={<Cart />} />
            <Route
                path={`${process.env.PUBLIC_URL}/scan-lost-item/:jsonData`}
                element={<ScannedProduct />}
            />
        </Routes>
    )
}

export default AppRoutes
