import {
    FormControl,
    InputLabel,
    MenuItem,
    Select,
    Input,
    TextField,
    FormControlLabel,
    Switch,
    Button,
    Box,
} from "@mui/material"
import { useFormik } from "formik"
import { useEffect, useState } from "react"
import { dateToday } from "../../helpers"
import { useAuthFormClasses } from "../../theming"

const Travel = () => {
    const categories = ["Train", "Bus", "Flight", "Metro", "Cab"]
    const [wouldRewarded, setWouldRewarded] = useState()
    const [vehicleLabel, setVehicleLabel] = useState('Train')
    const [vehicleNumber, setVehicleNumber] = useState('Train No(Optional)')
    const classes = useAuthFormClasses()

    const formik = useFormik({
        initialValues: {
            vcategory: "Train",
        },
        onSubmit: (values) => {
            console.log(values);
        }
    })

    useEffect(() => {
        console.log(formik.values.vcategory)
        switch (formik.values.vcategory) {
            case 'Train':
                setVehicleLabel('Train Name');
                break;
            case 'Flight':
                setVehicleLabel('AirLines');
                break;
            case 'Bus':
                setVehicleLabel('Bus Tour Travels');
                break;
            case 'Metro':
                setVehicleLabel('Metro City');
                break;
            case 'Cab':
                setVehicleLabel('Type of Cab');
                break;
            default:
                setVehicleLabel('Train');
        }

        switch (formik.values.vcategory) {
            case 'Train':
                setVehicleNumber('Train No(Optional)');
                break;
            case 'Flight':
                setVehicleNumber('Flight No');
                break;
            case 'Bus':
                setVehicleNumber('Bus No(Optional)');
                break;
            case 'Metro':
                setVehicleNumber('Metro Line');
                break;
            case 'Cab':
                setVehicleNumber('Cab No');
                break;
            default:
                setVehicleNumber('Train');
        }

    }, [formik.values.vcategory])

    return (
        <div>
            <div className={classes.inputWrapper}>
                <form onSubmit={formik.handleSubmit}>
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">
                            Choose Category
                        </InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            label="choose category"
                            id="vcategory"
                            name="vcategory"
                            onChange={formik.handleChange}
                            value={formik.values.vcategory}
                        >
                            {categories &&
                                categories.length > 0 &&
                                categories.map((category, index) => {
                                    return (
                                        <MenuItem value={category} key={category}>
                                            {category}
                                        </MenuItem>
                                    )
                                })}
                        </Select>
                    </FormControl>
                    <div className={classes.inputWrapper}>
                        <Input
                            required
                            id="image"
                            type="file"
                            label="Image"
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="typeOfLost"
                            label="Type of Lost"
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="contactNumber"
                            label="Contact Number"
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.inputWrapper}>
                        <FormControlLabel
                            value="1"
                            control={
                                <Switch
                                    color="primary"
                                    onClick={(e) => {
                                        setWouldRewarded(e.target.checked)
                                    }}
                                />
                            }
                            label="Would you like to give a reward?"
                            labelPlacement="start"
                        />
                    </div>
                    {wouldRewarded && (
                        <div className={classes.inputWrapper}>
                            <TextField
                                required
                                id="contactNumber"
                                label="Reward Rs."
                                autoComplete="off"
                                sx={{ width: "100%" }}
                            />
                        </div>
                    )}

                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            type="date"
                            id="contactNumber"
                            defaultValue={dateToday()}
                            label="Travelling Date"
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>

                    <div className={classes.inputWrapper}>
                        <InputLabel sx={{ fontSize: "1.2rem" }} color="success">
                            {formik.values.vcategory} Details
                        </InputLabel>
                    </div>

                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="vehicle"
                            label={vehicleLabel}
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="contactNumber"
                            label={vehicleNumber}
                            autoComplete="off"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Box sx={{ width: '48%' }}>
                            <div className={classes.inputWrapper}>
                                <TextField
                                    id="contactNumber"
                                    label="From"
                                    autoComplete="off"
                                    sx={{ width: "100%" }}
                                />
                            </div>
                        </Box>
                        <Box sx={{ width: '48%' }}>
                            <div className={classes.inputWrapper}>
                                <TextField
                                    id="contactNumber"
                                    label="Destination"
                                    autoComplete="off"
                                    sx={{ width: "100%" }}
                                />
                            </div>
                        </Box>
                    </Box>
                </form>
            </div>
            <div className={classes.formActionWrapper}>
                <Button variant="contained" type="submit">
                    Proceed
                </Button>
            </div>
        </div >
    )
}

export default Travel
