import {
    Card,
    CardActionArea,
    CardContent,
    CardMedia,
    Typography,
} from "@mui/material"
import { Link } from "react-router-dom"

export default function ProductCard({ product, id }) {
    return (
        <Card sx={{ mb: { xs: 2 } }}>
            <CardActionArea
                component={Link}
                to={`${process.env.PUBLIC_URL}/product/${id}/${product.id}`}
            >
                <CardMedia
                    component="img"
                    sx={{ width: 480, height: 300 }}
                    image={`https://s3.us-east-2.amazonaws.com/www.mfound.in/products_category/${product.image}`}
                    alt="green iguana"
                ></CardMedia>
                <CardContent>
                    <Typography variant="h5" align="center">
                        {product.name}
                    </Typography>
                </CardContent>
            </CardActionArea>
        </Card>
    )
}
