import { Button, TextField, Typography } from "@mui/material"
import React, { useContext, useEffect } from "react"
import { Link, useNavigate } from "react-router-dom"
import MFoundLogo from "../images/logo-mfound.png"
import { Box } from "@mui/system"
import { useAuthFormClasses } from "../theming"
import UserContext from "../context/UserContext"
import { useFormik } from "formik"
import axios from "axios"
import { ApiURL } from "../utils/Server"

const Register = () => {
    const classes = useAuthFormClasses()

    const userContext = useContext(UserContext)
    const userState = userContext.state
    const dispatch = userContext.useDispatch

    const navigate = useNavigate()

    useEffect(() => {
        if (userState.loggedIn && localStorage.getItem("barcode")) {
            navigate(
                `${
                    process.env.PUBLIC_URL
                }/scan-lost-item/${localStorage.getItem("barcode")}`
            )
        } else if (userState.loggedIn) {
            navigate("/")
        }
    }, [navigate, userState])

    const formik = useFormik({
        initialValues: {
            email: "",
            password: "",
        },
        onSubmit: (values) => {
            dispatch({ type: "REQUEST", payload: true })
            const names = values.fullname.split(" ")
            axios
                .post(`${ApiURL}auth/signup`, {
                    email: values.email,
                    password: values.password,
                    mobile: values.mobile,
                    firstName: names && names[0],
                    lastName: names.length > 0 && names[1],
                    device: "web",
                    role: "users",
                })
                .then((res) => {
                    if (typeof res.data.result !== "undefined")
                        dispatch({ type: "LOGIN", payload: res.data.result })
                })
                .catch((err) => {
                    alert("invalid data provided, try again")
                    dispatch({
                        type: "LOGOUT",
                    })
                })
                .then(() => {
                    dispatch({ type: "REQUEST", payload: false })
                })
        },
    })

    return (
        <div className={classes.root}>
            <div className={classes.formWrapper}>
                <form onSubmit={formik.handleSubmit}>
                    <div className={classes.logoWrapper}>
                        <Link to={`${process.env.PUBLIC_URL}`}>
                            <img src={MFoundLogo} width={250} alt="MFound" />
                        </Link>
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="fullname"
                            onChange={formik.handleChange}
                            value={formik.values.fullname}
                            label="Full Name"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="email"
                            onChange={formik.handleChange}
                            value={formik.values.email}
                            label="Email"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="password"
                            onChange={formik.handleChange}
                            value={formik.values.password}
                            label="Password"
                            type="password"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.inputWrapper}>
                        <TextField
                            required
                            id="mobile"
                            onChange={formik.handleChange}
                            value={formik.values.mobile}
                            label="Mobile"
                            sx={{ width: "100%" }}
                        />
                    </div>
                    <div className={classes.formActionWrapper}>
                        <Button variant="contained" type="submit">
                            Register
                        </Button>
                        <Box sx={{ display: "flex", padding: "20px 0" }}>
                            <Typography variant="body1" sx={{ mr: 1 }}>
                                Already have an account?
                            </Typography>
                            <Link to={`${process.env.PUBLIC_URL}/login`}>
                                login here
                            </Link>
                        </Box>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Register
