import axios from "axios"
import { ApiURL } from "../utils/Server"

export const fetchAllProducts = () => {
    let data = null
    axios
        .get(`${ApiURL}products/admin/all`)
        .then((res) => {
            if (res.data && typeof res.data.result !== "undefined") {
                console.log(res.data.result)
                return res.data.result
            } else {
                console.log("Api Error, didnt find the result")
            }
        })
        .catch((error) => {
            console.log(error)
        })
        .then(() => {
            console.log("returns nothing")
        })

    return data
}

export const fetchUser = async () => {
    let id = localStorage.getItem("m-id")
    try {
        let result = await axios.get(`${ApiURL}user/${id}`)
        return result
    } catch (err) {
        console.log(err)
        return null
    }
}

export const getCart = async () => {
    let id = localStorage.getItem("m-id")
    try {
        let result = await axios.get(`${ApiURL}cart/user/${id}`)
        return result
    } catch (err) {
        console.log(err)
        return null
    }
}

export const placeOrder = async (data) => {
    try {
        let result = await axios.post(`${ApiURL}place_order`, data, {
            headers: {
                "X_Auth_Token": localStorage.getItem("m-token")
            }
        })
        return result
    } catch (err) {
        console.log(err)
        return null
    }
}