import { Button, Container, Grid, Typography } from "@mui/material"
import { Box } from "@mui/system"
import Footer from "../components/Footer"
import Header from "../layouts/Header"
import LovedOne from "../images/loved-one.png"
import Bank from "../images/bank.png"
import React, { useContext, useEffect, useState } from "react"
import UserContext from "../context/UserContext"
import axios from "axios"
import CategoryCard from "../components/CategoryCard"
import { ApiURL } from "../utils/Server"

export default function Profile() {
    const [products, setProducts] = useState(null)
    const userContext = useContext(UserContext)
    const userState = userContext.state

    useEffect(() => {
        setProducts({ isTagLoading: true, tagProducts: null })
        axios
            .get(`${ApiURL}qr_codes/user/${localStorage.getItem("m-id")}`)
            .then((res) => {
                if (
                    typeof res.data === "object" &&
                    typeof res.data.result !== "undefined" &&
                    res.data.result !== null
                ) {
                    setProducts(res.data.result)
                }
            })
            .catch((err) => {
                console.log(err)
                setProducts(null)
            })
    }, [])

    return (
        <>
            <div className="head">
                <Header />
                <div className="services section-block">
                    <Container maxWidth="xl">
                        <div>
                            <Typography
                                variant="h4"
                                align="center"
                                sx={{ pb: 3 }}
                            >
                                My Tags
                            </Typography>
                            <Typography
                                variant="body1"
                                sx={{ textAlign: "center" }}
                            >
                                Lorem Ipsum has been the industry's standard
                                dummy text ever since the 1500s, when an unknown
                                printer took a
                            </Typography>
                            <Box
                                sx={{ py: 4 }}
                                display="flex"
                                justifyContent="space-between"
                                flexWrap="wrap"
                            >
                                {products !== null &&
                                    products.length > 0 &&
                                    products.map((item, index) => {
                                        return (
                                            <CategoryCard
                                                product={item}
                                                key={index}
                                                imagePath="qr_codes"
                                            />
                                        )
                                    })}
                            </Box>
                        </div>
                    </Container>
                </div>
                <div className="services section-block">
                    <Container maxWidth="xl">
                        <Grid
                            container
                            direction="row"
                            justify="space-between"
                            alignItems="center"
                        >
                            <Grid item md={6}>
                                <Typography
                                    variant="h4"
                                    component="h4"
                                    style={{ marginBottom: 20 }}
                                >
                                    {userState.profile.first_name}{" "}
                                    {userState.profile.last_name} (
                                    {userState.profile.mobile})
                                </Typography>
                                <Typography
                                    variant="h5"
                                    component="h5"
                                    style={{ marginBottom: 20 }}
                                    color="textSecondary"
                                >
                                    {userState.profile.email}
                                </Typography>
                                <Typography
                                    variant="body2"
                                    style={{ fontSize: 12, marginBottom: 5 }}
                                    color="textSecondary"
                                >
                                    Available Balance
                                </Typography>
                                <div
                                    style={{
                                        display: "inline-block",
                                        padding: 8,
                                        boxShadow:
                                            "rgba(0, 0, 0, 0.05) 0px 0px 0px 1px",
                                        borderRadius: 4,
                                    }}
                                >
                                    RS.{" "}
                                    {userState.profile.wallet === null
                                        ? 0
                                        : userState.profile.wallet}
                                    .00 /-
                                </div>
                            </Grid>
                            <Grid item md={6}>
                                <Box
                                    sx={{
                                        display: "flex",
                                        justifyContent: "flex-end",
                                    }}
                                >
                                    <img
                                        width="500"
                                        src={Bank}
                                        alt="add details of your loved one"
                                    />
                                </Box>
                            </Grid>
                        </Grid>
                    </Container>
                </div>
                <div className="bg-light section-block">
                    <Container maxWidth="xl">
                        <Box
                            display="flex"
                            justifyContent="space-between"
                            sx={{
                                py: { xs: 4, md: 8 },
                                flexWrap: { xs: "wrap", md: "nowrap" },
                                alignItems: "center",
                            }}
                        >
                            <Box sx={{ mb: { xs: 4, md: 0 } }}>
                                <Typography variant="h5" pb={2}>
                                    Add details of your child and loved ones to
                                    secure their future
                                </Typography>
                                <Typography
                                    variant="body1"
                                    sx={{
                                        textAlign: "left",
                                        mb: { xs: 4, md: 0 },
                                    }}
                                >
                                    Still thinking of reasons to include your
                                    child's and loved ones' information? Secure
                                    the information of your family members with
                                    us so that in the event of a mishap, we can
                                    assist you. Mfound is the precise location
                                    where you can get in touch with. The
                                    registration process is simple; all you have
                                    to do is upload an image of the individual,
                                    and our facial recognition technology will
                                    ensure a perfect match. Basic information
                                    about the person, such as name, parents'
                                    names, age, and so on, are requested in the
                                    personal information section. Mfound gives
                                    you the most options for becoming secure and
                                    eliminates the possibility of being lost or
                                    misplaced.
                                </Typography>
                                <Button variant="outlined" sx={{ my: 3 }}>
                                    Click here
                                </Button>
                            </Box>
                            <Box
                                sx={{
                                    minWidth: { sx: "100%", md: 700 },
                                }}
                            >
                                <img
                                    src={LovedOne}
                                    width="700"
                                    alt="Loved One MFound"
                                />
                            </Box>
                        </Box>
                    </Container>
                </div>
                <Footer />
            </div>
        </>
    )
}
