import {
    Box,
    Button,
    Container,
    Grid,
    IconButton,
    MenuItem,
    TextField,
    Typography,
} from "@mui/material"
import React, { useContext, useEffect, useState } from "react"
import Footer from "../components/Footer"
import UserContext from "../context/UserContext"
import Header from "../layouts/Header"
import { makeStyles } from "@mui/styles"
import { rupeeFy } from "../helpers"
import { Link } from "react-router-dom"
import DeleteIcon from "@mui/icons-material/Delete"
import axios from "axios"
import { ApiURL } from "../utils/Server"
import stateData from "../statedata.json"

const useStyles = makeStyles((theme) => ({
    root: {
        minHeight: "40vh",
    },
}))

export default function Cart() {
    const [totalAmount, setTotalAmount] = useState(null)
    const [address, setAddress] = useState({
        address: null,
        addressOptional: null,
        state: null,
        district: null,
        zip: null,
    })
    const [orderPlaced, setOrderPlaced] = useState(false)

    const [addressValidation, setAddressValidation] = useState({
        address: false,
        addressOptional: false,
        state: false,
        district: false,
        zip: false,
    })

    const userContext = useContext(UserContext)
    const userState = userContext.state
    const dispatch = userContext.useDispatch
    const { cart } = userState

    useEffect(() => {
        if (cart && cart.length > 0) {
            let amount = cart.reduce((accumulator, currentValue) => {
                return (
                    accumulator +
                    (currentValue.price * currentValue.quantity || 0)
                )
            }, 0)
            setTotalAmount(amount)
        }
    }, [cart])

    const loadScript = (src) => {
        return new Promise((resolve) => {
            const script = document.createElement("script")
            script.src = src
            script.onload = () => {
                resolve(true)
            }
            script.onerror = () => {
                resolve(false)
            }
            document.body.appendChild(script)
        })
    }

    useEffect(() => {
        loadScript("https://checkout.razorpay.com/v1/checkout.js")
    })

    const validationFields = () => {
        let error = false

        let { state, district, zip } = addressValidation
        let addressv = addressValidation.address

        if (!address.address) {
            error = true
            addressv = "Address is required."
        } else {
            addressv = null
        }

        if (!address.state) {
            error = true
            state = "State is required"
        } else {
            state = null
        }

        if (!address.district) {
            error = true
            district = "Address is required"
        } else {
            district = null
        }

        if (!address.zip) {
            error = true
            zip = "Address is required"
        } else {
            zip = null
        }

        if (error) {
            setAddressValidation({
                address: addressv,
                state,
                district,
                zip,
            })
            return false
        } else {
            return `Address Line: ${address.address}, State: ${address.state}, District: ${address.district}, Zip: ${address.zip}`
        }
    }

    const doCheckout = () => {
        let address = validationFields()
        if (address) {
            doProceed(address)
        }
    }

    const doProceed = async (address) => {
        axios
            .post(`${ApiURL}razor-pay-order-create`, {
                amount: parseInt(totalAmount, 10),
            })
            .then((res) => {
                const options = {
                    key: "rzp_live_nIdVWl4E1ul7dk",
                    currency: res.data.result.currency,
                    amount: res.data.result.amount,
                    name: "MFound",
                    description: "Purchasing key",
                    order_id: res.data.result.id,
                    handler: function (response) {
                        if (cart.length) {
                            dispatch({ type: "REQUEST", payload: true })
                            cart.forEach((item) => {
                                const orderData = {
                                    user_id: item.user_id,
                                    user_name: `${userState.profile.first_name} ${userState.profile.last_name}`,
                                    user_email: userState.profile.email,
                                    device_token: "webpurchase",
                                    product_id: item.product_id,
                                    quantity: item.quantity,
                                    address: address,
                                    total_amount: item.price,
                                    payment_type: "razorpay",
                                }

                                axios
                                    .post(`${ApiURL}place_order/`, orderData, {
                                        headers: {
                                            X_Auth_Token:
                                                localStorage.getItem("m-token"),
                                        },
                                    })
                                    .then((res) => {
                                        if (res.data !== "undefined") {
                                            if (res.status === 200) {
                                                axios
                                                    .delete(
                                                        `${ApiURL}cart/${item.cart_id}`
                                                    )
                                                    .then((res) => {
                                                        if (
                                                            res.status === 200
                                                        ) {
                                                            let updatedCart =
                                                                cart.filter(
                                                                    (
                                                                        cartItem
                                                                    ) =>
                                                                        cartItem.cart_id !==
                                                                        item.cart_id
                                                                )
                                                            dispatch({
                                                                type: "GET_CART",
                                                                payload: {
                                                                    cart: updatedCart,
                                                                },
                                                            })
                                                        }
                                                    })
                                                    .catch((err) => {
                                                        console.log(err)
                                                    })
                                            }
                                        }
                                    })
                                    .catch((err) => {
                                        console.log(err)
                                    })
                            })
                            dispatch({ type: "REQUEST", payload: false })
                        }
                    },
                    prefill: {
                        name: `${userState.profile.first_name} ${userState.profile.last_name}`,
                        email: userState.profile.email,
                        contact: userState.profile.mobile,
                    },
                }

                const paymentObject = new window.Razorpay(options)
                paymentObject.open()
                setOrderPlaced(true)
            })
            .catch((err) => {
                console.log(err)
            })
    }

    const classes = useStyles()

    const deleteItem = (item) => {
        dispatch({ type: "REQUEST", payload: true })
        axios
            .delete(`${ApiURL}cart/${item.cart_id}`)
            .then((res) => {
                if (res.data.result !== "undefined") {
                    let updatedCart = cart.filter(
                        (cartItem) => cartItem.cart_id !== item.cart_id
                    )
                    dispatch({
                        type: "GET_CART",
                        payload: { cart: updatedCart },
                    })
                }
            })
            .catch((err) => {
                console.log(err)
                alert("something went wrong, try later")
            })
            .then(() => {
                dispatch({ type: "REQUEST", payload: false })
            })
    }

    const handleChange = (e) => {
        setAddress({ ...address, [e.target.name]: e.target.value })
        setAddressValidation({ ...addressValidation, [e.target.name]: null })
    }

    return (
        <React.Fragment>
            <Header />
            <div className="section-block">
                <div className={classes.root}>
                    <Container maxWidth="xl">
                        {cart && cart.length ? (
                            <Grid container>
                                <Grid item xs={12} md={8}>
                                    <Typography variant="h4">
                                        Shopping Cart
                                    </Typography>
                                    <Grid
                                        container
                                        justifyContent={`space-between`}
                                        style={{
                                            borderTop: `1px solid rgba(0, 0, 0, 0.1)`,
                                            borderBottom: `1px solid rgba(0, 0, 0, 0.1)`,
                                            padding: `10px 0`,
                                            marginTop: 20,
                                        }}
                                    >
                                        <Grid item xs={4}>
                                            <Typography variant="body1">
                                                Product Details
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={4}>
                                            <Typography variant="body1">
                                                Quantity
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={2}>
                                            <Typography variant="body1">
                                                Price
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={2}>
                                            <Box
                                                display="flex"
                                                justifyContent="end"
                                            >
                                                <Typography variant="body1">
                                                    Total
                                                </Typography>
                                            </Box>
                                        </Grid>
                                    </Grid>
                                    <Grid
                                        container
                                        justifyContent={`space-between`}
                                        style={{
                                            padding: `10px 0`,
                                            marginTop: 20,
                                        }}
                                    >
                                        {cart &&
                                            cart.length > 0 &&
                                            cart.map((item, index) => {
                                                return (
                                                    <React.Fragment key={index}>
                                                        <Grid
                                                            item
                                                            xs={4}
                                                            sx={{ mb: 2 }}
                                                        >
                                                            <Grid container>
                                                                <Grid item>
                                                                    <Typography variant="bod1">
                                                                        {
                                                                            item.name
                                                                        }
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>
                                                        </Grid>
                                                        <Grid
                                                            item
                                                            xs={4}
                                                            sx={{ mb: 2 }}
                                                        >
                                                            <Box display="flex">
                                                                <Typography
                                                                    variant="body1"
                                                                    sx={{
                                                                        border: `1px solid rgba(0, 0, 0, 0.1)`,
                                                                        width: 70,
                                                                        borderRadius: 1,
                                                                        padding:
                                                                            "4px",
                                                                    }}
                                                                    marginRight={
                                                                        1
                                                                    }
                                                                    align="center"
                                                                >
                                                                    {
                                                                        item.quantity
                                                                    }
                                                                </Typography>
                                                                <IconButton
                                                                    variant="outlined"
                                                                    aria-label="delete"
                                                                    onClick={() => {
                                                                        deleteItem(
                                                                            item
                                                                        )
                                                                    }}
                                                                >
                                                                    <DeleteIcon />
                                                                </IconButton>
                                                            </Box>
                                                        </Grid>
                                                        <Grid
                                                            item
                                                            xs={2}
                                                            sx={{ mb: 2 }}
                                                        >
                                                            <Typography variant="body1">
                                                                &#x20B9;
                                                                {rupeeFy(
                                                                    item.price
                                                                )}
                                                            </Typography>
                                                        </Grid>
                                                        <Grid
                                                            item
                                                            xs={2}
                                                            sx={{ mb: 2 }}
                                                        >
                                                            <Box
                                                                display="flex"
                                                                justifyContent="end"
                                                            >
                                                                <Typography variant="body1">
                                                                    &#x20B9;
                                                                    {rupeeFy(
                                                                        item.price *
                                                                            item.quantity
                                                                    )}
                                                                </Typography>
                                                            </Box>
                                                        </Grid>
                                                    </React.Fragment>
                                                )
                                            })}
                                    </Grid>
                                    <Grid item mt={2}>
                                        <Link to={"/"}>
                                            <Button
                                                variant="outlined"
                                                size="small"
                                            >
                                                Continue Shopping
                                            </Button>
                                        </Link>
                                    </Grid>
                                </Grid>
                                <Grid
                                    item
                                    xs={12}
                                    md={4}
                                    sx={{ marginTop: { xs: 6, md: 0 } }}
                                >
                                    <Box
                                        display={`flex`}
                                        flexWrap="wrap"
                                        sx={{
                                            mx: { md: 4 },
                                            p: 2,
                                            backgroundColor: `rgb(155 151 151 / 10%)`,
                                            boxShadow: `rgba(0, 0, 0, 0.16) 0px 1px 4px`,
                                            borderRadius: 1,
                                        }}
                                    >
                                        <Box sx={{ width: `100%` }}>
                                            <Typography
                                                variant="h5"
                                                sx={{
                                                    paddingBottom: 2,
                                                    borderBottom: `1px solid rgba(0, 0, 0, 0.1)`,
                                                }}
                                            >
                                                Order Summary
                                            </Typography>
                                        </Box>
                                        <Box
                                            sx={{ width: "100%", marginTop: 2 }}
                                            display="flex"
                                            justifyContent={`space-between`}
                                        >
                                            <Box>
                                                <Typography variant="body1">
                                                    ITEMS&nbsp;
                                                    {cart && cart.length}
                                                </Typography>
                                            </Box>
                                            <Box>
                                                <Typography variant="body1">
                                                    &#x20B9;
                                                    {totalAmount !== null &&
                                                        rupeeFy(totalAmount)}
                                                </Typography>
                                            </Box>
                                        </Box>
                                        <Box
                                            sx={{
                                                width: "100%",
                                                marginTop: 2,
                                                borderBottom: `1px solid rgba(0, 0, 0, 0.1)`,
                                                paddingBottom: 4,
                                            }}
                                            display="flex"
                                            justifyContent={`space-between`}
                                        >
                                            <Box>
                                                <Typography variant="body1">
                                                    SHIPPING COST
                                                </Typography>
                                            </Box>
                                            <Box>
                                                <Typography variant="body1">
                                                    Free
                                                </Typography>
                                            </Box>
                                        </Box>
                                        <Box
                                            sx={{ width: "100%", marginTop: 2 }}
                                            display="flex"
                                            justifyContent={`space-between`}
                                        >
                                            <Box>
                                                <Typography variant="body1">
                                                    TOTAL
                                                </Typography>
                                            </Box>
                                            <Box>
                                                <Typography variant="body1">
                                                    &#x20B9;
                                                    {totalAmount !== null &&
                                                        rupeeFy(
                                                            parseInt(
                                                                totalAmount
                                                            )
                                                        )}
                                                </Typography>
                                            </Box>
                                        </Box>
                                        <Box sx={{ mt: 2, width: "100%" }}>
                                            <TextField
                                                variant="outlined"
                                                size="small"
                                                name="address"
                                                value={address?.address}
                                                onChange={handleChange}
                                                label="Address Line One"
                                                required
                                                fullWidth
                                                error={
                                                    addressValidation.address
                                                }
                                                helperText={
                                                    addressValidation.address
                                                }
                                            />
                                            <TextField
                                                sx={{ mt: 2 }}
                                                variant="outlined"
                                                size="small"
                                                name="addressOptional"
                                                value={address?.addressOptional}
                                                onChange={handleChange}
                                                label="Address Line two (Optional)"
                                                fullWidth
                                            />
                                            <TextField
                                                select
                                                label="Select State"
                                                variant="outlined"
                                                sx={{ mt: 2 }}
                                                fullWidth
                                                size="small"
                                                name="state"
                                                value={address?.state}
                                                onChange={handleChange}
                                                error={addressValidation.state}
                                                helperText={
                                                    addressValidation.state
                                                }
                                            >
                                                {stateData?.states.map(
                                                    (item) => (
                                                        <MenuItem
                                                            key={item.state}
                                                            value={item.state}
                                                        >
                                                            {item.state}
                                                        </MenuItem>
                                                    )
                                                )}
                                            </TextField>
                                            <TextField
                                                select
                                                label="Select District"
                                                variant="outlined"
                                                sx={{ mt: 2 }}
                                                fullWidth
                                                size="small"
                                                name="district"
                                                value={address?.district}
                                                onChange={handleChange}
                                                error={
                                                    addressValidation.district
                                                }
                                                helperText={
                                                    addressValidation.district
                                                }
                                            >
                                                {stateData?.states.findIndex(
                                                    (i) =>
                                                        i.state ===
                                                        address.state
                                                ) >= 0 &&
                                                    stateData?.states[
                                                        stateData?.states.findIndex(
                                                            (i) =>
                                                                i.state ===
                                                                address.state
                                                        )
                                                    ].districts.map((item) => (
                                                        <MenuItem
                                                            key={item}
                                                            value={item}
                                                        >
                                                            {item}
                                                        </MenuItem>
                                                    ))}
                                            </TextField>
                                            <TextField
                                                sx={{ mt: 2 }}
                                                variant="outlined"
                                                type="number"
                                                size="small"
                                                name="zip"
                                                value={address?.zip}
                                                onChange={handleChange}
                                                label="Zip Code"
                                                fullWidth
                                                error={addressValidation.zip}
                                                helperText={
                                                    addressValidation.zip
                                                }
                                            />
                                        </Box>
                                        <Box
                                            sx={{ width: "100%", marginTop: 2 }}
                                        >
                                            <Button
                                                variant="contained"
                                                fullWidth
                                                onClick={doCheckout}
                                            >
                                                Checkout
                                            </Button>
                                        </Box>
                                    </Box>
                                </Grid>
                            </Grid>
                        ) : (
                            <>
                                {orderPlaced && (
                                    <Box sx={{ mb: 4 }}>
                                        <Typography variant="h5" align="center">
                                            Your Order has been successfully
                                            placed, it will be delivered to your
                                            door within 5 working days.
                                        </Typography>
                                        <Typography variant="h6" align="center">
                                            Thanks for shopping with us
                                        </Typography>
                                    </Box>
                                )}
                                <Box display="flex" justifyContent={`center`}>
                                    <Box>
                                        <Link to={"/"}>
                                            <Button
                                                variant="outlined"
                                                size="small"
                                            >
                                                Continue Shopping
                                            </Button>
                                        </Link>
                                    </Box>
                                </Box>
                            </>
                        )}
                    </Container>
                </div>
            </div>
            <Footer />
        </React.Fragment>
    )
}
